import { atom } from "recoil";

export const registerState = atom({
    key: "register",
    default: false,
});

export interface IToDo {
    id: number;
    text: string;
}

interface IToDoState {
    [key : string] : IToDo[];
}

export const toDoState = atom<IToDoState>({
    key: "toDo",
    default: localStorage.getItem("toDo")?.length !== undefined ? JSON.parse(localStorage.getItem("toDo") || "") : {},
});